body {
  color: #333;
  background: linear-gradient(135deg, #74ebd5 0%, #acb6e5 100%);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 40px 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.startBtn {
  color: #fff;
  cursor: pointer;
  background-color: #4a90e2;
  border: none;
  border-radius: 8px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  box-shadow: 0 4px 12px #00000026;
}

.startBtn:hover:not(:disabled) {
  background-color: #357bd8;
}

.startBtn:disabled {
  cursor: not-allowed;
  background-color: #b0c9e8;
}

.message-text {
  color: #333;
  min-height: 40px;
  margin-top: 30px;
  font-size: 24px;
}

.box-container {
  gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

.box {
  border-radius: 10px;
  width: 50px;
  height: 50px;
  transition: all .3s;
  position: relative;
}

.red {
  background-color: #e74c3c;
}

.green {
  background-color: #2ecc71;
}

.blue {
  background-color: #3498db;
}

.startAnimation {
  color: #fff;
  cursor: pointer;
  background: #4a90e2;
  border: none;
  border-radius: 8px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

.startGame {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 18px;
  transition: background .3s;
}

.startGame:disabled {
  cursor: default;
  background: #888;
}

.gameArea {
  background: #fff;
  border: 2px solid #ccc;
  width: 300px;
  height: 300px;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #0000001a;
}

.target {
  cursor: pointer;
  background: #dc143c;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transition: background .2s;
  display: none;
  position: absolute;
}

.score, .text-game {
  margin-top: 10px;
  font-size: 20px;
}

.timeInput {
  border: 2px solid #74ebd5;
  border-radius: 10px;
  outline: none;
  width: 140px;
  margin-bottom: 20px;
  margin-left: 20px;
  padding: 12px 16px;
  font-size: 18px;
  transition: border-color .3s;
}

.timeInput:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 8px #4a90e280;
}

.startTimer {
  color: #fff;
  cursor: pointer;
  background-color: #4a90e2;
  border: none;
  border-radius: 10px;
  margin-left: 15px;
  padding: 12px 25px;
  font-size: 18px;
  transition: background-color .3s, box-shadow .3s;
  box-shadow: 0 5px 12px #4a90e266;
}

.startTimer:hover:not(:disabled) {
  background-color: #357abd;
  box-shadow: 0 7px 15px #357abd99;
}

.startTimer:disabled {
  cursor: not-allowed;
  box-shadow: none;
  background-color: #a0a0a0;
}

.outputMessage {
  color: #333;
  -webkit-user-select: none;
  user-select: none;
  min-height: 30px;
  margin-top: 25px;
  font-size: 22px;
  font-weight: 600;
  transition: color .3s;
}
